import * as THREE from "three";
import { cloneDeep } from "lodash";
import {
  EditBBoxChildSuffix,
  getThreeNameFromEditBBoxId,
} from "../editorObjects/EditBBox";
import { appThree } from "./renderThreejs";
import { getStore } from "../controllers/store";
import { logger } from "../logger";

let selectBBoxInstance: SelectBBox | null = null;

export type SelectBBoxRect = {
  id: string;
  position: THREE.Vector3;
  width: number;
  height: number;
  depth: number;
  rotation: THREE.Vector3;
};

export const getSelectBBox = (): SelectBBox => {
  if (selectBBoxInstance == null) {
    selectBBoxInstance = new SelectBBox();
  }
  return selectBBoxInstance;
};

export const clearSelectBBox = () => {
  selectBBoxInstance = null;
};

export class SelectBBox {
  rotation: THREE.Vector3;
  position: THREE.Vector3;
  width: number;
  height: number;
  depth: number;
  rectangles: SelectBBoxRect[];
  internalId: string;
  dotBrName: string;
  dotBlName: string;
  dotTrName: string;
  dotTlName: string;
  selectBboxGroupName: string;
  selectBBoxBgName: string;
  boxGeometry: THREE.BoxGeometry;
  obj: THREE.Mesh<THREE.BoxGeometry, THREE.Material | THREE.Material[]>;
  layerRoot: number;
  layerChild: number;
  worldPosEnd: { x: number; y: number };
  startPos: number[][];

  constructor() {
    this.worldPosEnd = { x: 0, y: 0 };
    this.startPos = [];
    this.layerRoot = 2;
    this.layerChild = 3;
    this.rotation = new THREE.Vector3(0, 0, 0);
    this.position = new THREE.Vector3(0, 0, 0);
    this.width = 0;
    this.height = 0;
    this.depth = 0;
    this.rectangles = [];
    this.internalId = THREE.MathUtils.generateUUID();
    this.selectBboxGroupName = getThreeNameFromEditBBoxId(this.internalId);
    this.selectBBoxBgName = getThreeNameFromEditBBoxId(
      this.internalId,
      EditBBoxChildSuffix.BG
    );
    this.dotBrName = getThreeNameFromEditBBoxId(
      this.internalId,
      EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
    );
    this.dotBlName = getThreeNameFromEditBBoxId(
      this.internalId,
      EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
    );
    this.dotTrName = getThreeNameFromEditBBoxId(
      this.internalId,
      EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
    );
    this.dotTlName = getThreeNameFromEditBBoxId(
      this.internalId,
      EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
    );
    this.boxGeometry = new THREE.BoxGeometry(1, 1, 1);
    this.obj = new THREE.Mesh(this.boxGeometry);
  }

  isChild(intersects: THREE.Intersection[]) {
    let result = false;
    intersects.forEach((i) => {
      if (i.object.userData.name === this.selectBBoxBgName) {
        result = true;
      }
    });
    return result;
  }
  onDownMove() {
    const selectBBoxBgName = appThree.objects.get(this.selectBBoxBgName);
    if (selectBBoxBgName) {
      this.startPos.push([
        selectBBoxBgName.position.x,
        selectBBoxBgName.position.y,
      ]);
    }
    const dotBlName = appThree.objects.get(this.dotBlName);
    if (dotBlName) {
      this.startPos.push([dotBlName.position.x, dotBlName.position.y]);
    }
    const dotBrName = appThree.objects.get(this.dotBrName);
    if (dotBrName) {
      this.startPos.push([dotBrName.position.x, dotBrName.position.y]);
    }
    const dotTlName = appThree.objects.get(this.dotTlName);
    if (dotTlName) {
      this.startPos.push([dotTlName.position.x, dotTlName.position.y]);
    }
    const dotTrName = appThree.objects.get(this.dotTrName);
    if (dotTrName) {
      this.startPos.push([dotTrName.position.x, dotTrName.position.y]);
    }
  }

  onMoveMove(diffX: number, diffY: number) {
    const selectBBoxGoup = appThree.objects.get(this.selectBBoxBgName);
    if (selectBBoxGoup) {
      selectBBoxGoup.position.x = this.startPos[0][0] + diffX;
      selectBBoxGoup.position.y = this.startPos[0][1] + diffY;
    }
    const dotBlName = appThree.objects.get(this.dotBlName);
    if (dotBlName) {
      dotBlName.position.x = this.startPos[1][0] + diffX;
      dotBlName.position.y = this.startPos[1][1] + diffY;
    }
    const dotBrName = appThree.objects.get(this.dotBrName);
    if (dotBrName) {
      dotBrName.position.x = this.startPos[2][0] + diffX;
      dotBrName.position.y = this.startPos[2][1] + diffY;
    }
    const dotTlName = appThree.objects.get(this.dotTlName);
    if (dotTlName) {
      dotTlName.position.x = this.startPos[3][0] + diffX;
      dotTlName.position.y = this.startPos[3][1] + diffY;
    }
    const dotTrName = appThree.objects.get(this.dotTrName);
    if (dotTrName) {
      dotTrName.position.x = this.startPos[4][0] + diffX;
      dotTrName.position.y = this.startPos[4][1] + diffY;
    }
  }

  onUpMove() {
    this.startPos = [];
    this.syncRectsWithStore();
  }

  isHandle(intersects: THREE.Intersection[]) {
    let result = false;
    intersects.forEach((i) => {
      if (i.object.layers.mask === 8) {
        result = true;
      }
    });
    return result;
  }
  onDownEdit(clientX: number, clientY: number) {
    // throw new Error("Method not implemented.");
  }
  onMoveEdit(clientX: number, clientY: number) {
    // throw new Error("Method not implemented.");
  }
  onUpEdit(clientX: number, clientY: number) {
    // throw new Error("Method not implemented.");
  }

  syncRectsWithStore() {
    const store = getStore();
    const rectangles: SelectBBoxRect[] = [];
    this.rectangles.forEach(({ id }) => {
      const object = store.objects[id];
      if (!object) {
        logger.warn("missing id", id);
        return;
      }
      rectangles.push({
        depth: object.depth,
        height: object.height,
        width: object.width,
        id: object.id,
        position: new THREE.Vector3(
          object.position.x,
          object.position.y,
          object.position.z
        ),
        rotation: new THREE.Vector3(
          object.rotateX,
          object.rotateY,
          object.rotateZ
        ),
      });
    });
    this.rectangles = rectangles;

    const bbox = this.getSelectBBox();
    this.position = new THREE.Vector3();
    bbox.getCenter(this.position);
    this.width = bbox.max.x - bbox.min.x;
    this.height = bbox.max.y - bbox.min.y;
    this.depth = bbox.max.z - bbox.min.z;
    this.updateHandleAndBg(bbox);
  }

  updateRect(rectangle: SelectBBoxRect, isSelected: boolean) {
    let toDelete = false;
    let toCreate = false;
    const rectIndex = this.rectangles.findIndex((e) => e.id === rectangle.id);
    const isRectExists = rectIndex !== -1;

    if (isSelected === true && isRectExists === false) {
      toCreate = true;
    } else if (isSelected === false && isRectExists === true) {
      toDelete = true;
    }

    if (toCreate === false && toDelete === false) {
      return;
    }

    if (toDelete) {
      this.rectangles.splice(rectIndex, 1);
    }

    if (toCreate) {
      this.rectangles.push(cloneDeep(rectangle));
    }

    if (this.rectangles.length === 1) {
      this.rotation = this.rectangles[0].rotation;
    } else {
      this.rotation = new THREE.Vector3(0, 0, 0);
    }

    const bbox = this.getSelectBBox();
    this.position = new THREE.Vector3();
    bbox.getCenter(this.position);
    this.width = bbox.max.x - bbox.min.x;
    this.height = bbox.max.y - bbox.min.y;
    this.depth = bbox.max.z - bbox.min.z;

    if (this.rectangles.length === 0) {
      this.deleteHandleAndBg();
    } else {
      const object = appThree.objects.get(this.selectBboxGroupName);

      if (object === undefined) {
        this.createHandleAndBg(bbox);
      }
      this.updateHandleAndBg(bbox);
    }
  }

  deleteHandleAndBg() {
    const object = appThree.objects.get(this.selectBboxGroupName);
    if (object !== undefined) {
      if (object.parent !== undefined && object.parent !== null) {
        object.parent.remove(object);
      }
    }
    appThree.objects.delete(this.dotBlName);
    appThree.objects.delete(this.dotBrName);
    appThree.objects.delete(this.dotTlName);
    appThree.objects.delete(this.dotTrName);
    appThree.objects.delete(this.selectBBoxBgName);
    appThree.objects.delete(this.selectBboxGroupName);
  }

  createHandleAndBg(bbox: THREE.Box3) {
    // create bbox
    const selectBBoxGoup = new THREE.Group();
    selectBBoxGoup.userData.name = this.selectBboxGroupName;

    const geometry = new THREE.PlaneGeometry(1, 1, 1, 1);
    const material = new THREE.MeshBasicMaterial({
      color: "rgb(0,0,0)",
      side: THREE.FrontSide,
      opacity: 0.2,
      transparent: true,
    });
    const selectBBoxBg = new THREE.Mesh(geometry, material);
    selectBBoxBg.userData.name = this.selectBBoxBgName;

    const selectBBoxLines = this.getRectLine(-0.5, 0.5, -0.5, 0.5);
    selectBBoxBg.add(selectBBoxLines);

    const resizeDots = this.getResizeDots(bbox);

    resizeDots.forEach((dot) => {
      appThree.objects.set(dot.userData.name, dot);
      selectBBoxGoup.attach(dot);
    });

    selectBBoxGoup.add(selectBBoxBg);
    appThree.scene.add(selectBBoxGoup);

    appThree.objects.set(this.selectBboxGroupName, selectBBoxGoup);
    appThree.objects.set(this.selectBBoxBgName, selectBBoxBg);
  }

  getRectLine(x1: number, x2: number, y1: number, y2: number) {
    const rectShape = new THREE.Shape()
      .moveTo(x1, y1)
      .lineTo(x2, y1)
      .lineTo(x2, y2)
      .lineTo(x1, y2);

    rectShape.autoClose = true;

    const _rectLinesPoints = rectShape.getPoints();
    const _rectLinesGeometryPoints = new THREE.BufferGeometry().setFromPoints(
      _rectLinesPoints
    );
    const _rectLinesMaterial = new THREE.MeshBasicMaterial({
      color: 0x0040f0,
      opacity: 1,
      transparent: true,
    });
    return new THREE.Line(_rectLinesGeometryPoints, _rectLinesMaterial);
  }

  getRectBBox(rect: SelectBBoxRect) {
    const { width, height, depth, position, rotation } = rect;
    const bbox = new THREE.Box3();
    this.obj.scale.set(width, height, depth);
    this.obj.position.set(position.x, position.y, position.z);
    this.obj.rotation.set(
      THREE.MathUtils.DEG2RAD * rotation.x,
      THREE.MathUtils.DEG2RAD * rotation.y,
      THREE.MathUtils.DEG2RAD * rotation.z
    );
    bbox.expandByObject(this.obj);
    return bbox;
  }

  getSelectBBox() {
    const bbox = new THREE.Box3();
    this.rectangles.forEach((rect) => {
      const { width, height, depth, position, rotation } = rect;
      this.obj.scale.set(width, height, depth);
      this.obj.position.set(position.x, position.y, position.z);
      this.obj.rotation.set(
        THREE.MathUtils.DEG2RAD * rotation.x,
        THREE.MathUtils.DEG2RAD * rotation.y,
        THREE.MathUtils.DEG2RAD * rotation.z
      );
      bbox.expandByObject(this.obj);
    });
    return bbox;
  }

  updateHandleAndBg(bbox: THREE.Box3) {
    const selectBBoxBg = appThree.objects.get(this.selectBBoxBgName);
    if (selectBBoxBg !== undefined) {
      selectBBoxBg.position.set(
        this.position.x,
        this.position.y,
        this.position.z
      );
      selectBBoxBg.scale.set(this.width, this.height, this.depth);
      // _bg.rotation.set(this.rotation.x, this.rotation.y, this.rotation.z);

      selectBBoxBg.clear();

      const selectBBoxLines = this.getRectLine(-0.5, 0.5, -0.5, 0.5);
      selectBBoxBg.add(selectBBoxLines);

      if (this.rectangles.length >= 2) {
        const position = new THREE.Vector3();
        bbox.getCenter(position);
        const width = bbox.max.x - bbox.min.x;
        const height = bbox.max.y - bbox.min.y;
        this.rectangles.forEach((rect) => {
          const _bbox = this.getRectBBox(rect);
          const _pos = new THREE.Vector3();
          _bbox.getCenter(_pos);
          const centerX = (_pos.x - position.x) / width;
          const centerY = (_pos.y - position.y) / height;
          const _w = (_bbox.max.x - _bbox.min.x) / width;
          const _h = (_bbox.max.y - _bbox.min.y) / height;
          const x1 = centerX - _w / 2;
          const x2 = centerX + _w / 2;
          const y1 = centerY - _h / 2;
          const y2 = centerY + _h / 2;
          const _line = this.getRectLine(x1, x2, y1, y2);
          // debugger;
          selectBBoxBg.add(_line);
        });
      }
    }

    const _dotTl = appThree.objects.get(this.dotTlName);
    if (_dotTl !== undefined) {
      const v1 = new THREE.Vector3(
        bbox.min.x - this.position.x,
        bbox.min.y - this.position.y,
        bbox.min.z - this.position.z
      );
      _dotTl.position.set(
        this.position.x + v1.x,
        this.position.y + v1.y,
        this.position.z + v1.z
      );
    }

    const _dotTr = appThree.objects.get(this.dotTrName);
    if (_dotTr !== undefined) {
      const v1 = new THREE.Vector3(
        bbox.max.x - this.position.x,
        bbox.min.y - this.position.y,
        bbox.min.z - this.position.z
      );
      _dotTr.position.set(
        this.position.x + v1.x,
        this.position.y + v1.y,
        this.position.z + v1.z
      );
    }

    const _dotBl = appThree.objects.get(this.dotBlName);
    if (_dotBl !== undefined) {
      const v1 = new THREE.Vector3(
        bbox.min.x - this.position.x,
        bbox.max.y - this.position.y,
        bbox.min.z - this.position.z
      );
      _dotBl.position.set(
        this.position.x + v1.x,
        this.position.y + v1.y,
        this.position.z + v1.z
      );
    }

    const _dotBr = appThree.objects.get(this.dotBrName);
    if (_dotBr !== undefined) {
      const v1 = new THREE.Vector3(
        bbox.max.x - this.position.x,
        bbox.max.y - this.position.y,
        bbox.min.z - this.position.z
      );
      _dotBr.position.set(
        this.position.x + v1.x,
        this.position.y + v1.y,
        this.position.z + v1.z
      );
    }
  }

  getResizeDots = (bbox: THREE.Box3) => {
    const objects: THREE.Object3D[] = [];
    const sceneScale = appThree.camera.userData["sceneScale"];
    const dotSize = sceneScale; //(rect10x10FromScreenToWorld.w ?? 10) * 0.5;

    const __material = new THREE.MeshLambertMaterial({ color: "#ffffff" });
    const __boxGeometry = new THREE.CircleGeometry(1, 16);
    const _whiteDot = new THREE.Mesh(__boxGeometry, __material);
    _whiteDot.scale.x = 10;
    _whiteDot.scale.y = 10;
    _whiteDot.renderOrder = 2;
    _whiteDot.layers.set(this.layerChild);

    const ___material = new THREE.MeshLambertMaterial({ color: "#a9a9a9" });
    const ___boxGeometry = new THREE.CircleGeometry(1, 16);
    const _grayDot = new THREE.Mesh(___boxGeometry, ___material);
    _grayDot.scale.x = 12;
    _grayDot.scale.y = 12;
    _grayDot.renderOrder = 1;
    _grayDot.layers.set(this.layerChild);

    const _baseDot = new THREE.Group();
    _baseDot.layers.set(this.layerRoot);
    _baseDot.attach(_grayDot);
    _baseDot.attach(_whiteDot);

    const _dotTL = _baseDot.clone();
    _dotTL.position.x = bbox.min.x;
    _dotTL.position.y = bbox.max.y;
    _dotTL.scale.x = dotSize;
    _dotTL.scale.y = dotSize;
    _dotTL.userData.name = this.dotTlName;
    objects.push(_dotTL);

    const _dotTR = _baseDot.clone();
    _dotTR.position.x = bbox.max.x;
    _dotTR.position.y = bbox.max.y;
    _dotTR.scale.x = dotSize;
    _dotTR.scale.y = dotSize;
    _dotTR.userData.name = this.dotTrName;
    objects.push(_dotTR);

    const _dotBL = _baseDot.clone();
    _dotBL.position.x = bbox.min.x;
    _dotBL.position.y = bbox.min.y;
    _dotBL.scale.x = dotSize;
    _dotBL.scale.y = dotSize;
    _dotBL.userData.name = this.dotBlName;
    objects.push(_dotBL);

    const _dotBR = _baseDot.clone();
    _dotBR.position.x = bbox.max.x;
    _dotBR.position.y = bbox.min.y;
    _dotBR.scale.x = dotSize;
    _dotBR.scale.y = dotSize;
    _dotBR.userData.name = this.dotBrName;
    objects.push(_dotBR);
    return objects;
  };
}
